import React, { Component } from 'react';
import Page from '../Logic/Page';
import StyledH1 from '../styledComponents/StyledH1';
import StyledH3 from '../styledComponents/StyledH3';
import StyledBorderBox from '../styledComponents/StyledBorderBox';
import StyledParagraph from '../styledComponents/StyledParagraph';

class Evenements extends Component {
  render() {
    return (
      <Page>
        <StyledH1>vnements</StyledH1>
        <StyledParagraph>venement compos de 3 courses:</StyledParagraph>
        <StyledParagraph>Le 21 aot: Un sprint urbain</StyledParagraph>
        <StyledParagraph>Le 22 aot: Une moyenne distance</StyledParagraph>
        <StyledParagraph>Le 23 aot: La Nationale, une longue distance</StyledParagraph>
        <StyledBorderBox>
          <StyledH3>Sprint</StyledH3>
          <StyledParagraph>Le sprint aura lieu le 21 aot</StyledParagraph>
        </StyledBorderBox>
        <StyledBorderBox>
          <StyledH3>MD</StyledH3>
          <StyledParagraph>La MD aura lieu le 22 aot</StyledParagraph>
        </StyledBorderBox>
        <StyledBorderBox>
          <StyledH3>LD</StyledH3>
          <StyledParagraph>La LD aura lieu le 21 aot</StyledParagraph>
        </StyledBorderBox>
      </Page>
    );
  }
}

export default Evenements;



