const style = {
  fontSize: {
    big: '2rem',
    medium: '1.5rem',
    small: '1rem'
  },
  mediaSize: {
    mobileS: '320px',
    mobileM: '375px',
    mobileL: '425px',
    tablet: '768px',
    laptop: '1024px',
    laptopL: '1440px',
    desktop: '2560px'
  }
};

export default style;



